section {
	padding: 30px;
	margin: 30px;
	border-bottom: 3px solid lightgreen;
	h2:first-child {
		color: red; } }

/*~~~~~~~~~~~~~~~ END section CONTENT-ADDSERVIC ~~~~~~~~~~~~~~~*/


.horiz-menu {
	display: flex;
	justify-content: center;
	& > li {
		position: relative;
		margin: 0 10px;
		background: #12fd45;
		& > a {
			display: block;
			padding: 15px 30px;
			color: #fff;
			text-decoration: none;
			white-space: nowrap; //без переноса слов
			&:after {
				content: '';
				display: inline-block;
				margin-left: 10px;
				vertical-align: middle;
				@include arr (10, 10, red, b); } } }


	//Меню при наведении
	.hover-menu {
		display: none;
		position: absolute;
		top: 53px;
		left: 0;
		z-index: 99;
		li {
			background: lightblue;
			a {
				display: block;
				padding: 15px 30px;
				color: #fff;
				text-decoration: none;
				white-space: nowrap; } } } //без переноса слов
	.main-item:hover ~ .hover-menu,
	.hover-menu:hover {
		display: block; }

	//Меню при клике
	.click-menu {
		display: none;
		position: absolute;
		top: 53px;
		left: 0;
		z-index: 99;
		li {
			background: pink;
			a {
				display: block;
				padding: 15px 30px;
				color: #fff;
				text-decoration: none;
				white-space: nowrap; } } } //без переноса слов
	.main-item:focus ~ .click-menu,
	.main-item:active ~ .click-menu,
	.click-menu:hover {
		display: block; } }



//Hover effects
.hover--item_1 {
	position: relative;
	@include size (120px 60px);
	background: #ccc;
	overflow: hidden;
	cursor: pointer;
	&:after {
		content: '';
		display: block;
		position: absolute;
		width: 100%;
		height: 5px;
		bottom: 0;
		right: 100%;
		background: #000;
		transition: right 0.2s ease-in-out; }
	&:hover:after {
		right: 0; } }




//Tabs
.tabs {
	background: #EFEFEF;
	margin: 30px 0; }
.tabs__caption {
	height: 28px;
	line-height: 25px;
	list-style: none;
	border-bottom: 1px solid #DDD;
	background: #FFF;
	li {
		display: inline-block;
		margin: 0 -5px -1px 0;
		padding: 0 13px 1px;
		color: #777;
		cursor: pointer;
		background: #F9F9F9;
		border: 1px solid #E4E4E4;
		border-bottom: 1px solid #F9F9F9;
		position: relative;
		&:hover {
			color: #F70;
			padding: 0 13px;
			background: #FFFFDF;
			border: 1px solid #FFCA95; } } }
.tabs__caption .active {
	color: #444;
	background: #EFEFEF;
	padding: 0 13px 2px;
	border: 1px solid #D4D4D4;
	border-bottom: 1px solid #EFEFEF; }

.tabs__content {
	display: none;
	border: 1px solid #D4D4D4;
	border-width: 0 1px 1px;
	background: #EFEFEF;
	padding: 0 12px;
	&.active {
		display: block; } }


//Slider
$btn-size: 50px;

.owl-carousel {
	position: relative;
	margin: 30px 0;
	.owl-stage-outer {
		@include border-bottom-radius($btn-size / 2); }
	.owl-nav > div {
		position: absolute;
		@include size($btn-size);
		bottom: 0;
		background: red;
		border-radius: 50%;
		&:after {
			font-family: "FontAwesome";
			display: block;
			@include size($btn-size);
			text-align: center;
			line-height: $btn-size;
			font-size: 35px;
			color: #fff;
			transition: font-size .3s ease;
			border-radius: 50%;
			box-shadow: 0 2px 1px rgba(0, 0, 0, 0.1), 0 1px 3px rgba(0, 0, 0, 0.3); }
		&:hover:after {
			font-size: 45px; } }
	.owl-nav {
		.owl-prev {
			left: 0;
			&:after {
				content: '\f060'; } }
		.owl-next {
			right: 0;
			&:after {
				content: '\f061'; } } }
	.owl-dots {
		position: absolute;
		top: 0;
		.owl-dot {
			display: inline-block;
			@include size (10px);
			background: #000;
			margin: 5px; } } }


